import { convertToRaw } from 'draft-js';
import Resizer from 'react-image-file-resizer';
import api from '../Api';

function getTotalSize(images) {
  return images.reduce((partialSum, image) => partialSum + image.size, 0);
}

function resizeImage(imageFile, height, handleCallback) {
  return new Promise((resolve) => {
    Resizer.imageFileResizer(
      imageFile,
      10000,
      height,
      'WEBP',
      100,
      0,
      (uri) => {
        handleCallback();
        resolve(uri);
      },
      'file',
      0,
      height,
    );
  });
}

function UploadImages(
  bucket,
  memoryKey,
  title,
  imageFiles,
  incrementImageCount,
) {
  const paramList = imageFiles.map((imgFile) => (
    {
      Key: ['memories', memoryKey, 'images', imgFile.name].join('/'),
      Body: imgFile,
      ContentType: imgFile.type,
    }
  ));
  const uploadPromises = api.uploadMultipleImages(bucket, paramList, incrementImageCount);
  return Promise.allSettled(uploadPromises);
}

function ResizeImages(resizeHeights, imageFiles, handleCallback) {
  return Promise.all(
    resizeHeights.map(async (resizeHeight) => {
      const resizedImages = await Promise.all(
        imageFiles.map((image) => resizeImage(image, resizeHeight, handleCallback)),
      );
      return { [resizeHeight]: resizedImages };
    }),
  );
}

async function UploadResizedImages(bucket, resizedImages, memoryKey) {
  const params = [];
  resizedImages.forEach((heightDetails) => (
    Object.entries(heightDetails).forEach(([height, images]) => (
      images.forEach((image) => {
        params.push({
          Key: `memories/${memoryKey}/images/resized/${height}h/${image.name}`,
          Body: image,
        });
      })))
  ));
  const uploadPromises = api.uploadMultipleImages(bucket, params, () => {});
  return Promise.allSettled(uploadPromises);
}

async function InitMemory(
  bucket,
  memoryKey,
  imageDetails,
  thumbnail,
  title,
  date,
  editorState,
) {
  const memoryDetails = {
    key: memoryKey,
    title,
    date,
    lastModified: new Date(),
    imageDetails,
    contentState: convertToRaw(editorState.getCurrentContent()),
    thumbnail: imageDetails[thumbnail],
    totalSize: getTotalSize(imageDetails),
  };

  const memoryDetailsKey = ['memories', memoryDetails.key, 'details.json'].join('/');
  const objectType = 'application/json';
  const resp = await api.upload(
    bucket,
    memoryDetailsKey,
    JSON.stringify(memoryDetails),
    objectType,
  );
  return resp;
}

const InitializeMemory = async (
  bucket,
  memoryKey,
  imageDetails,
  thumbnail,
  title,
  date,
  editorState,
  handleProgress,
  updateMessageStatus,
  addMessage,
) => {
  handleProgress(0);
  addMessage('Initializing memory', false);
  const initMemResults = await InitMemory(
    bucket,
    memoryKey,
    imageDetails,
    thumbnail,
    title,
    date,
    editorState,
  );
  updateMessageStatus('Initializing Memory', true);
  handleProgress(10);
  return initMemResults;
};

const PrepareThumbnail = async (
  bucket,
  memoryKey,
  thumbnail,
  title,
  addMessage,
  updateMessageStatus,
  handleProgress,
) => {
  addMessage('Preparing thumbnail', false);
  const resizedThumbnail = await resizeImage(thumbnail, 700, () => {});
  const thumbnailResults = await api.upload(
    bucket,
    `memories/${memoryKey}/thumbnail.png`,
    resizedThumbnail,
    resizedThumbnail.type,
  );
  updateMessageStatus('Preparing thumbnail', true);
  handleProgress(20);
  return thumbnailResults;
};

const HandleImageResize = async (
  bucket,
  memoryKey,
  imageFiles,
  resizeHeights,
  handleProgress,
  updateMessageStatus,
  addMessage,
  incrementProgress,
) => {
  addMessage('Resizing Images', false);
  const resizedImages = await ResizeImages(
    resizeHeights,
    imageFiles,
    () => { incrementProgress(1); },
  );
  handleProgress(70);
  updateMessageStatus('Resizing Images', true);
  addMessage('Uploading Resized Images', false);
  const resizeResults = await UploadResizedImages(bucket, resizedImages, memoryKey);
  updateMessageStatus('Uploading Resized Images', true);
  handleProgress(80);
  return resizeResults;
};

const HandleImages = async (
  bucket,
  memoryKey,
  title,
  imageFiles,
  incrementImageCount,
  handleProgress,
) => {
  const imagesResults = await UploadImages(
    bucket,
    memoryKey,
    title,
    imageFiles,
    incrementImageCount,
  );
  handleProgress(100);
  return imagesResults;
};

export default {
  HandleImageResize,
  InitializeMemory,
  PrepareThumbnail,
  HandleImages,
};
