import { useParams } from 'react-router-dom';
import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import { EditorState, convertFromRaw } from 'draft-js';

import api from '../Api';
import BasePage from '../base_page';
import MemoryContent from './MemoryContent';
import MemoryContentLoading from './MemoryContentLoading';

const getDetails = async (bucket, memoryId, handleCallback) => {
  const memoryKey = `memories/${memoryId}/details.json`;
  const data = await api.getS3Key(bucket, memoryKey);
  handleCallback(data);
  return data;
};

function changeExt(fileName, newExt) {
  const pos = fileName.includes('.') ? fileName.lastIndexOf('.') : fileName.length;
  const fileRoot = fileName.substr(0, pos);
  const output = `${fileRoot}.${newExt}`;
  return output;
}

const getMemoryImages = (bucket, memoryId, memoryData, handleCallback) => {
  const defaultImageHeight = 500;
  memoryData.imageDetails.forEach(async (image) => {
    const resizedFileName = changeExt(image.name, 'WEBP');
    const defaultImageKey = `memories/${memoryId}/images/resized/${defaultImageHeight}h/${resizedFileName}`;
    const defaultImage = await api.getObjectUrl(bucket, defaultImageKey, 'GET');
    const imageList = image.images.filter(
      (imageDimension) => imageDimension.height !== defaultImageHeight,
    );
    const images = await Promise.all(imageList.map(async (imageDimension) => {
      const imageUrl = await api.getObjectUrl(
        bucket,
        `memories/${memoryId}/images/resized/${imageDimension.height}h/${resizedFileName}`,
        'GET',
      );
      return { ...imageDimension, src: imageUrl };
    }));

    handleCallback({
      src: defaultImage,
      width: Math.ceil((image.width * defaultImageHeight) / image.height),
      height: defaultImageHeight,
      images,
    });
  });
};

async function getMemory(
  bucket,
  memoryId,
  handleDetailsCallback,
  handleImagesCallback,
) {
  try {
    const details = await getDetails(bucket, memoryId, handleDetailsCallback);
    getMemoryImages(bucket, memoryId, details, handleImagesCallback);
  } catch (error) {
    console.log(error);
  }
}

export default function ViewMemory({ bucket }) {
  const params = useParams();
  const { memoryId } = params;
  const [details, setDetails] = useState(null);
  const [images, setImages] = useState([]);

  const addImages = (image) => {
    setImages((arr) => arr.concat([image]));
  };

  const handleMemoryDetails = (det) => setDetails(det);

  useEffect(() => {
    getMemory(bucket, memoryId, handleMemoryDetails, addImages);
  }, []);

  return (details && (details.imageDetails.length === images.length)) ? (
    <BasePage>
      <MemoryContent
        title={details.title}
        date={details.date}
        images={images}
        editorState={EditorState.createWithContent(convertFromRaw(details.contentState))}
      />
    </BasePage>
  ) : (
    <BasePage>
      <MemoryContentLoading />
    </BasePage>
  );
}

ViewMemory.propTypes = {
  bucket: PropTypes.string.isRequired,
};
