import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import useTitle from '../../hooks/title';
import useDate from '../../hooks/date';
import useSelectedImages from '../../hooks/selectedImages';
import useStory from '../../hooks/story';
import useStep from '../../hooks/formStep';
import useProgressMessage from '../../hooks/progressMessage';

import CreateMemoryForm from './CreateMemoryForm';
import PreviewMemory from './PreviewMemory';
import TitleDateForm from './TitleDateForm';
import ImageForm from './ImageForm';
import StoryForm from './StoryForm';
import CreateMemoryProgress from './CreateMemoryProgress';
import MemoryUtils from './MemoryUtils';

export default function ModeContent({ mode, bucket }) {
  const navigate = useNavigate();
  const resizeHeights = [500, 1000, 1500];
  const stepTitles = [
    'Enter memory title and date',
    'Upload Pictures',
    'Write story',
  ];
  const [activeStep, handleNext, handlePrevious, isLast, isFirst] = useStep(stepTitles.length);
  const [title, handleTitleChange] = useTitle();
  const [date, handleDateChange] = useDate();
  const [
    imageDetails,
    thumbnail,
    handleSelectedImageChange,
    removeSelectedImage,
    handleThumbnailChange,
    setDimensions,
  ] = useSelectedImages();

  const [editorState, handleEditorStateChange] = useStory();
  const [open, setOpen] = useState(false);
  const [progress, setProgress] = useState(0);
  const [messages, addMessage, updateMessageStatus] = useProgressMessage();
  const [uploadCount, setUploadCount] = useState(0);

  const handleProgress = (progressPercentage) => setProgress(progressPercentage);
  const incrementProgress = (progressPercentage) => setProgress((prg) => prg + progressPercentage);

  const incrementImageCount = () => setUploadCount((cnt) => cnt + 1);

  const imageFiles = imageDetails.map((detail) => detail.imageFile);

  const TitleDate = (
    <TitleDateForm
      activeStep={activeStep}
      handleNext={handleNext}
      handlePrevious={handlePrevious}
      isLast={isLast}
      isFirst={isFirst}
      title={title}
      date={date}
      handleTitleChange={handleTitleChange}
      handleDateChange={handleDateChange}
    />
  );

  const SelectImages = (
    <ImageForm
      imageDetails={imageDetails}
      handleSelectedImageChange={handleSelectedImageChange}
      removeSelectedImage={removeSelectedImage}
      thumbnail={thumbnail}
      handleThumbnailChange={handleThumbnailChange}
      handleNext={handleNext}
      handlePrevious={handlePrevious}
      isLast={isLast}
      isFirst={isFirst}
      setDimensions={setDimensions}
      resizeHeights={resizeHeights}
    />
  );

  useEffect(() => {
    const message = `Uploading Original Images (${uploadCount}/${imageDetails.length})`;
    if (uploadCount >= 0 && progress >= 80) {
      if (uploadCount === 0) addMessage(message, false);
      else updateMessageStatus(message, false);

      if (imageDetails.length === uploadCount) updateMessageStatus(message, true);
    }
  }, [uploadCount, progress]);

  const handleSubmit = async () => {
    // so that the newest memory has the smallest key and so  comes first in s3 listObjects
    // If this fails wont be there to take blame. Should be dead by then. :-)
    const memoryKey = new Date(2100, 1, 1).getTime() - Date.now();
    setOpen(true);
    const initMemResults = await MemoryUtils.InitializeMemory(
      bucket,
      memoryKey,
      imageDetails,
      thumbnail,
      title,
      date,
      editorState,
      handleProgress,
      updateMessageStatus,
      addMessage,
    );
    const thumbnailResults = await MemoryUtils.PrepareThumbnail(
      bucket,
      memoryKey,
      imageDetails[thumbnail].imageFile,
      title,
      addMessage,
      updateMessageStatus,
      handleProgress,
    );
    const resizeResults = await MemoryUtils.HandleImageResize(
      bucket,
      memoryKey,
      imageFiles,
      resizeHeights,
      handleProgress,
      updateMessageStatus,
      addMessage,
      incrementProgress,
    );
    const imagesResults = await MemoryUtils.HandleImages(
      bucket,
      memoryKey,
      title,
      imageFiles,
      incrementImageCount,
      handleProgress,
    );
    console.log('all results', resizeResults, initMemResults, thumbnailResults, imagesResults);
    setTimeout(() => navigate('/memories'), 10000);
  };

  const ProgressDialog = (
    <CreateMemoryProgress
      open={open}
      progress={progress}
      messages={messages}
      handleNavigate={() => navigate('/memories')}
    />
  );

  const WriteStory = (
    <StoryForm
      editorState={editorState}
      handleEditorStateChange={handleEditorStateChange}
      handleNext={handleSubmit}
      handlePrevious={handlePrevious}
      isLast={isLast}
      isFirst={isFirst}
      progressDialog={ProgressDialog}
    />
  );

  const stepForms = [TitleDate, SelectImages, WriteStory];

  let content;

  const CreateModeContent = CreateMemoryForm(
    stepTitles.length,
    stepTitles,
    stepForms,
    handleSubmit,
    activeStep,
    isLast,
    handleNext,
    handlePrevious,
  );
  const PreviewModeContent = PreviewMemory(title, date, imageDetails, thumbnail, editorState);
  if (mode === 'Create') {
    content = CreateModeContent;
  }

  if (mode === 'Preview') {
    content = PreviewModeContent;
  }

  return content;
}
