import * as React from 'react';
import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import BasePage from '../base_page';
import GridView from './GridView';
import api from '../Api';

async function getMemories(bucket, handleMemoryCallback, handleTokenCallback, nextToken = '') {
  try {
    const resp = await api.getMemoryList(bucket, nextToken);
    if (resp.CommonPrefixes) {
      const memoryKeys = resp.CommonPrefixes && resp.CommonPrefixes.map((commonPref) => `${commonPref.Prefix}details.json`);
      const details = await Promise.all(api.getMemoryDetails(bucket, memoryKeys));
      details.map(async (detail) => {
        const thumbnailKey = ['memories', detail.key, 'thumbnail.png'].join('/');
        const thumbnailImage = await api.getObjectUrl(bucket, thumbnailKey, 'GET');
        handleMemoryCallback([{ ...detail, thumbnailImage }]);
      });

      handleTokenCallback(resp.NextContinuationToken || '');
    }
  } catch (error) {
    console.log(error);
  }
}

export default function Memories({ bucket }) {
  const [nextToken, setNextToken] = useState('');
  const [memories, setMemories] = useState([]);

  const handleTokenChange = (newToken) => {
    setNextToken(newToken);
  };

  const addMemories = (details) => {
    setMemories((arr) => arr.concat(details));
  };

  useEffect(() => {
    getMemories(bucket, addMemories, handleTokenChange, nextToken);
  }, []);

  return (
    <BasePage>
      <GridView
        memories={memories}
        nextToken={nextToken}
        handleMoreClick={() => getMemories(bucket, addMemories, handleTokenChange, nextToken)}
      />
    </BasePage>
  );
}

Memories.propTypes = {
  bucket: PropTypes.string.isRequired,
};
